//Global Resets
img {
    display: block;
    max-width: 100%;
    height: auto;
}

p,
h1,
h2,
h3,
h4,
h5,
h6 {
    margin: 0;
}

ul {
    list-style: none;
    margin: 0;
    padding: 0;
}

a {
    text-decoration: none;
}

textarea {
    resize: none;
}

:active,
:hover,
:focus {
    outline: 0;
    outline-offset: 0;
}

*,
*::before,
*::after {
    box-sizing: border-box;
}